.taroify-notify {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: var(--notify-padding, var(--padding-xs, 16px) var(--padding-md, 32px));
  font-size: var(--notify-font-size, var(--font-size-md, 28px));
  line-height: var(--notify-line-height, var(--line-height-md, 40px));
  color: var(--notify-color, var(--white, #fff));
  text-align: center;
  word-wrap: break-word;
  white-space: pre-wrap;
}
.taroify-notify--primary {
  background: var(--notify-primary-background-color, var(--primary-color, var(--blue, #1989fa)));
}
.taroify-notify--success {
  background: var(--notify-success-background-color, var(--success-color, var(--green, #07c160)));
}
.taroify-notify--danger {
  background: var(--notify-danger-background-color, var(--danger-color, var(--red, #ee0a24)));
}
.taroify-notify--warning {
  background: var(--notify-warning-background-color, var(--warning-color, var(--orange, #ff976a)));
}